jQuery(document).ready(function($){
    if(!$('body').hasClass('single-product')) return;
    
     $('.product-gallery > .infinitishine-slider').each(function(){
       var _this = $(this);
       
       var _slick = _this.slick({
            infinite: true,
            mobileFirst:true,
            slidesToShow: 1,
            slidesToScroll: 1,
            focusOnSelect: true,
            asNavFor:_this.closest('.product-gallery-wrapper').find('.product-gallery-nav > .infinitishine-slider'),
            appendArrows:   _this.parent(),
            nextArrow:  "<div class='slider-control right'>"+
                          "<i class='fa fa-chevron-right'></i>"+
                        "</div>",
            prevArrow:  "<div class='slider-control left'>"+
                          "<i class='fa fa-chevron-left'></i>"+
                        "</div>",
        });
    });
    
     $('.product-gallery-nav > .infinitishine-slider').each(function(){
       var _this = $(this);
       
       var _slick = _this.slick({
            infinite: true,
            mobileFirst:true,
            slidesToShow: 10,
            slidesToScroll: 1,
            focusOnSelect: true,
            asNavFor:_this.closest('.product-gallery-wrapper').find('.product-gallery > .infinitishine-slider'),
            appendArrows: false,
            responsive: [
                {
                  breakpoint: BN_VARS.BREAKPOINTS.breakpoint_lg,
                  settings: {
                    slidesToShow: 10,
                    slidesToScroll: 1,
                  }
                },
                {
                  breakpoint: BN_VARS.BREAKPOINTS.breakpoint_sm,
                  settings: {
                    slidesToShow: 10,
                    slidesToScroll: 1
                  }
                }
            ]
        });
    });
    
    $('.related-products > .gallery').each(function(){
        var _this = $(this);
       
        var _slick = _this.slick({
            infinite: false,
            mobileFirst:true,
            slidesToShow: 1,
            slidesToScroll: 1,
            focusOnSelect: true,
            appendArrows:   _this.closest('.related-products-wrapper'),
            nextArrow:  "<div class='slider-control right'>"+
                          "<i class='fa fa-chevron-right'></i>"+
                        "</div>",
            prevArrow:  "<div class='slider-control left'>"+
                          "<i class='fa fa-chevron-left'></i>"+
                        "</div>",
            responsive: [
              {
                  breakpoint: BN_VARS.BREAKPOINTS.breakpoint_sm,
                  settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2
                  }
                },
                {
                  breakpoint: BN_VARS.BREAKPOINTS.breakpoint_lg,
                  settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                  }
                },
                {
                  breakpoint: BN_VARS.BREAKPOINTS.breakpoint_sl,
                  settings: {
                    slidesToShow: 4,
                    slidesToScroll: 4
                  }
                }
            ]
        });
    });
});