/**
 * Site wide scripts
 */
 !function(window){
  var $q = function(q, res){
        if (document.querySelectorAll) {
          res = document.querySelectorAll(q);
        } else {
          var d=document
            , a=d.styleSheets[0] || d.createStyleSheet();
          a.addRule(q,'f:b');
          for(var l=d.all,b=0,c=[],f=l.length;b<f;b++)
            l[b].currentStyle.f && c.push(l[b]);

          a.removeRule(0);
          res = c;
        }
        return res;
      }
    , addEventListener = function(evt, fn){
        window.addEventListener
          ? this.addEventListener(evt, fn, false)
          : (window.attachEvent)
            ? this.attachEvent('on' + evt, fn)
            : this['on' + evt] = fn;
      }
    , _has = function(obj, key) {
        return Object.prototype.hasOwnProperty.call(obj, key);
      }
    ;

  function loadImage (el, fn) {
    var img = new Image()
      , src = el.getAttribute('data-src');
    img.onload = function() {
      if (!! el.parent)
        el.parent.replaceChild(img, el)
      else
        el.src = src;

      fn? fn() : null;
    }
    img.src = src;
  }

  function elementInViewport(el) {
    var rect = el.getBoundingClientRect()

    return (
       rect.top    >= 0
    && rect.left   >= 0
    && rect.top <= (window.innerHeight || document.documentElement.clientHeight)
    )
  }

    var images = new Array()
      , query = $q('img.lazy')
      , processScroll = function(){
          for (var i = 0; i < images.length; i++) {
            if (elementInViewport(images[i])) {
              loadImage(images[i], function () {
                images.splice(i, i);
              });
            }
          };
        }
      ;
    // Array.prototype.slice.call is not callable under our lovely IE8 
    for (var i = 0; i < query.length; i++) {
      images.push(query[i]);
    };

    processScroll();
    addEventListener('scroll',processScroll);
}(this);

jQuery(document).ready(function($){
   //$('footer.footer').footerReveal();
   
   $('.g-recaptcha iframe').on('load',function(){
        console.log('recaptcha loaded now');
   });
    
    $("a.btn-menu").click(function(e){
        $(this).closest('.infinitishine-page-wrapper').toggleClass('off-canvas-expanded');
    });
    
    $(window).scroll(function() {
    if($('header.infinitishine-page-menu-wrapper').length < 1) return false;
    
		if ($(this).scrollTop() > $('header.infinitishine-page-menu-wrapper').position().top ){  
			$('header.infinitishine-page-menu-wrapper').addClass("stuck");
		}else{
			$('header.infinitishine-page-menu-wrapper').removeClass("stuck");
		}
	});
    
    bus.$on('awesomeCartMiniAddToCart', function(_ele){
        _ele.siblings('.fa').fadeIn('fast').addClass('adding');
    })
    
    bus.$on('awesomeCartMiniAddedToCart', function(_ele, added){
        var added = added ? 'fa-check-circle':'fa-exclamation-circle text-danger';
        
        _ele.siblings('.fa').removeClass('fa-spinner fa-pulse fa-fw').addClass(added);
    });
});

